<template>
    <a-row>
        <a-col :span="4">
            <a-menu
                    v-model="current"
                    mode="horizontal"
                    theme="dark">
                <a-menu-item>
                    <a-icon type="mail"/>
                    智慧党建大数据管理系统
                </a-menu-item>
            </a-menu>
        </a-col>
        <a-col :span="20">
            <a-menu
                    @select="handleSelect"
                    v-model="current"
                    mode="horizontal"
                    theme="dark">
                <a-sub-menu style="float: right">
                        <span slot="title">
                            <a-icon type="setting"/>操作</span>
                    <a-menu-item key="setting:1">设置</a-menu-item>
                    <a-menu-item key="setting:2">登出</a-menu-item>
                </a-sub-menu>
            </a-menu>
        </a-col>
    </a-row>
</template>
<script>
    import {logout} from "@/api/user";

    export default {
        data() {
            return {
                current: ['mail'],
            }
        },
        methods: {
            handleSelect(obj) {
                // console.log(obj.key);
                switch (obj.key) {
                    case 'setting:1':
                        break;
                    case 'setting:2':
                        logout()
                            .then(()=>{
                                this.$info({
                                    title: '提示',
                                    content: (
                                        <div>
                                            <p>登出成功</p>
                                        </div>
                                    ),
                                })
                                this.$router.push({name: 'login'});
                            })
                        break;
                }
            }
        }
    }
</script>
