<template>
    <a-menu
            @select="handleSelect"
            mode="inline"
            theme="light">
        <a-sub-menu
                v-for="(menu,index) in menus"
                v-if="!(menu.hasOwnProperty('visible')&&!menu.visible)"
                :key="menu.key">
            <span slot="title">
                <a-icon :type="menu.icon"/>
                <span>{{menu.name}}</span>
            </span>
            <a-menu-item
                    v-for="(m,i) in menu.menus"
                    :key="m.routeName">
                {{m.name}}
            </a-menu-item>
        </a-sub-menu>
    </a-menu>
</template>

<script>
    import menus from "@/dictionaries/superMenu";

    export default {
        data() {
            return {
                menus
            }
        },
        methods: {
            setBreadcrumb(routeName) {
                let temp = [];
                for (let i = 0; i < this.menus.length; i++) {
                    for (let j = 0; j < this.menus[i].menus.length; j++) {
                        if (this.menus[i].menus[j].routeName === routeName) {
                            temp = [
                                {name: this.menus[i].name},
                                {name: this.menus[i].menus[j].name},
                            ];
                        }
                    }
                }
                // console.log(routeName, temp, this.menus);

                if (temp.length) {
                    // console.log(this.$store.state.superAdmin);
                    this.$store.state.superAdmin.breadcrumb = temp;
                }
            },
            handleSelect(obj) {
                let key = obj.key;
                // console.log(key);
                // return
                // if (!key || key === '') {
                //     this.$info({
                //         title: '提示',
                //         content: (
                //             <div>
                //                 <p>功能开发中...</p>
                //             </div>
                //         ),
                //     })
                //     return;
                // }


                this.$router.push({name: key});
                this.setBreadcrumb(key);
            }
        }
    }
</script>
